import React from "react";
import Slider from "react-slick";
import "../../styles/imageZoom.css";
import InnerImageZoom from "react-inner-image-zoom";

export const ZoomableCarousel = ({ images }) => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    adaptiveHeight: true,
  };
  return (
    <Slider {...settings} style={{ marginRight: "20px" }}>
      {images.map((img) => (
        <div>
          <InnerImageZoom src={img} fullscreenOnMobile={true} />
        </div>
      ))}
    </Slider>
  );
};
